'use strict';

/**
* Cube root of double-precision floating-point epsilon.
*
* @module @stdlib/constants/float64/cbrt-eps
* @type {number}
*
* @example
* import FLOAT64_CBRT_EPSILON from '@stdlib/constants/float64/cbrt-eps';
* // returns 0.0000060554544523933395
*/


// MAIN //

/**
* Cube root of double-precision floating-point epsilon.
*
* ```tex
* \sqrt[3]{\frac{1}{2^{52}}}
* ```
*
* @constant
* @type {number}
* @default 0.0000060554544523933395
* @see [IEEE 754]{@link https://en.wikipedia.org/wiki/IEEE_754-1985}
* @see [Machine Epsilon]{@link https://en.wikipedia.org/wiki/Machine_epsilon}
*/
var FLOAT64_CBRT_EPSILON = 6.05545445239333906078989272793696693569753008995e-6;


// EXPORTS //

export default FLOAT64_CBRT_EPSILON;
