'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-read-only-property';


// MAIN //

/**
* Top-level namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name MAX
* @memberof ns
* @readonly
* @type {number}
* @see {@link module:@stdlib/constants/unicode/max}
*/
import MAX from './../../unicode/max';
setReadOnly( ns, 'MAX', MAX );

/**
* @name MAX_BMP
* @memberof ns
* @readonly
* @type {number}
* @see {@link module:@stdlib/constants/unicode/max-bmp}
*/
import MAX_BMP from './../../unicode/max-bmp';
setReadOnly( ns, 'MAX_BMP', MAX_BMP );


// EXPORTS //

export default ns;
