'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-read-only-property';


// MAIN //

/**
* Top-level namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name NUM_BYTES
* @memberof ns
* @readonly
* @constant
* @type {number}
* @see {@link module:@stdlib/constants/complex128/num-bytes}
*/
import NUM_BYTES from './../../complex128/num-bytes';
setReadOnly( ns, 'NUM_BYTES', NUM_BYTES );

/**
* @name ZERO
* @memberof ns
* @readonly
* @constant
* @type {Complex128}
* @see {@link module:@stdlib/constants/complex128/zero}
*/
import ZERO from './../../complex128/zero';
setReadOnly( ns, 'ZERO', ZERO );


// EXPORTS //

export default ns;
